import React, { useState, useEffect } from 'react'
import Footer from './components/Footer'
import Note from './components/Note'
import Notification from './components/Notification'
import noteService from './services/notes'

const App = () => {
  const [notes, setNotes] = useState([])
  const [newNote, setNewNote] = useState('')
  // const [showAll, setShowAll] = useState(false)
  const [errorMessage, setErrorMessage] = useState(null)
  const [popupActive, setPopupActive] = useState(false);

  useEffect(() => {
    noteService
      .getAll()
      .then(initialNotes => {
      setNotes(initialNotes)
    })
  }, [])

  const randomLetter = String.fromCharCode(65 + Math.floor(Math.random() * 26));

  const addNote = (event) => {
    event.preventDefault()
    const noteObject = {
      id: randomLetter + Date.now(),
      content: newNote,
      date: new Date().toISOString(),
      // important: Math.random() < 0.5,
    }

    noteService
      .create(noteObject)
        .then(returnedNote => {
        setNotes(notes.concat(returnedNote))
        setNewNote('')
      })
  }

  const checkForErrors = id => {
    const note = notes.find(n => n.id === id)
    const changedNote = { ...note}
    noteService
    .update(id, changedNote)
      .then(returnedNote => {
      setNotes(notes.map(note => note.id !== id ? note : returnedNote))
    })
    .catch(error => {
      setErrorMessage(
        `Note '${note.content}' was already removed from server`
      )
      setTimeout(() => {
        setErrorMessage(null)
      }, 5000)
    })    
  }


  // const toggleImportanceOf = id => {
  //   const note = notes.find(n => n.id === id)
  //   const changedNote = { ...note, important: !note.important }
  //   noteService
  //   .update(id, changedNote)
  //     .then(returnedNote => {
  //     setNotes(notes.map(note => note.id !== id ? note : returnedNote))
  //   })
  //   .catch(error => {
  //     setErrorMessage(
  //       `Note '${note.content}' was already removed from server`
  //     )
  //     setTimeout(() => {
  //       setErrorMessage(null)
  //     }, 5000)
  //   })    
  // }

  const handleNoteChange = (event) => {
    console.log(event.target.value)
    setNewNote(event.target.value)
  }


  return (
    <div className="App">
      <h1>Grocery List</h1>
      <Notification message={errorMessage} />
      <div>
        {/* <button onClick={() => setShowAll(!showAll)}>
          show {showAll ? 'important' : 'all' }
        </button> */}
      </div>   
      <ul>
        {notes.length > 0 ? notes.map(note => (
            <Note
              key={note.id}
              note={note} 
              checkForErrors={() => checkForErrors(note.id)}
            />
        )) : (
          <p>You currently have no tasks</p>
        )}
      </ul>
      <div>
            <div className="addPopup" onClick={() => setPopupActive(true)}><i class="fas fa-plus"></i></div>
    
            {popupActive ? (
                <div className="popup">
                    <div className="closePopup" onClick={() => setPopupActive(false)}><i class="fas fa-times"></i></div>
                    <div className="content">
                        <h3>Add Item</h3>
                        <form onSubmit={addNote}>
                            <input 
                                type="text" 
                                className="add-list-input" 
                                value={newNote}
                                onChange={handleNoteChange}
                            />
                            <br/>
                            <button type="submit">add</button>
                        </form>
                        
                    </div>
                </div>
            ) : ''}
        </div>

      <Footer />  
    </div>
  )
}

export default App



