import React from 'react'
import './Note.css'


const Note = ({ note }) => {
  // const label = note.important
  //   ? 'make not important' : 'make important'

  return (
    <li className="note">
      {note.content} 
      {/* <button onClick={toggleImportance}>{label}</button> */}
      <div className="checkbox"></div>
    </li>
  )
}

export default Note